export default {
	/**
	 * 
	 * @param {*} timestamp 
	 * @returns -- ::
	 */
	formatTime(timestamp) {
		const d = new Date(parseInt(timestamp)); //
		const date = (d.getFullYear()) + "-" +
			(d.getMonth() + 1) + "-" +
			(d.getDate()) + " " +
			(d.getHours()) + ":" +
			(d.getMinutes()) + ":" +
			(d.getSeconds());
		return date;
	},
	/**
	 * 
	 * @param {*} timestamp 
	 * @returns :
	 */
	formatHour(timestamp) {
		const d = new Date(parseInt(timestamp)); //
		const date =
			(d.getHours()) + ":" +
			(d.getMinutes())
		return date;
	},
	/**
	 * , 
	 * @param {any} data
	 * @returns  
	 */
	deepRemove(data) {
		// 
		if (typeof data !== 'object') return data
		// null undefined 
		if (data == undefined) return data
		//  RegExp Date Function 
		if (data instanceof RegExp) return new RegExp(data)
		if (data instanceof Date) return new Date(data)
		if (data instanceof Function) return new Function(data)
		// 
		let newData = new data.constructor()
		// : undefinednull
		for (let key in data) {
			if (data.hasOwnProperty(key)) {
				let res = this.deepRemove(data[key])
				// , (0)
				if ((res || res === 0) && JSON.stringify(res) !== '[]' && JSON.stringify(res) !== '{}') {
					newData[key] = res
				}
			}
		}

		return newData
	},

	// deepRemove
	// 
	removeEmpty(data) {
		const res = this.deepRemove(data)
		let finalData;
		// data, empty, 
		if (res instanceof Array) {
			finalData = res.filter(item => item || item === 0)
		} else {
			// data,
			finalData = res
		}

		return finalData
	},

	/**
	 * 
	 * @param  obj 
	 * @returns newObj
	 */
	deepClone(obj) {
		// 
		if (typeof obj !== 'object') return obj

		// null undefined 
		if (obj == undefined) return obj

		//  RegExp Date Function 
		if (obj instanceof RegExp) return new RegExp(obj)
		if (obj instanceof Date) return new Date(obj)
		if (obj instanceof Function) return new Function(obj)

		// constructor, 
		let newObj = new obj.constructor()
		for (let key in obj) {
			if (obj.hasOwnProperty(key)) {
				newObj[key] = this.deepClone(obj[key])
			}
		}

		return newObj
	},

	/**
	 * 
	 */
	mergefoods(foods) {
		let data = this.deepClone(foods);
		// let data = foods;
		let foodInfoArray = [];
		const classifyArray = []
		// 
		for (let i = 0; i < data.length; i++) {
			classifyArray.push(data[i].food_classify)
		}
		// 
		const newClassifyArray = [...new Set(classifyArray)]

		for (let i in newClassifyArray) {
			const o = {
				classify: newClassifyArray[i],
				list: []
			}

			for (let j in classifyArray) {
				if (data[j].food_classify === newClassifyArray[i]) {
					o.list.push(data[j])
				}
			}

			foodInfoArray.push(o);
		}

		return foodInfoArray
	},
	/**
	 * (id)
	 * @param {Object} orders 
	 * @returns Object data
	 */
	dealOrderData(orders) {
		let data = this.deepClone(orders);

		let current_id, next_id;
		let foodInfoArray = [];
		let foodDataArray = [];

		for (let i = 0; i < data.length; i++) {
			const temp = {
				shop_id: data[i].shop_info.id,
				data: {
					shop_info: data[i].shop_info,
					food_data: ""
				}
			}
			current_id = data[i].id;
			// next_idundefined
			// , foodInfoArray
			if (data[i + 1]) {
				next_id = data[i + 1].id;
			} else {
				foodDataArray.push(data[i].food_info)
				temp.data.food_data = foodDataArray
				foodInfoArray.push(temp)
				data[i].food_info = foodInfoArray
				return data;
			}
			// 
			// idfood_info
			if (current_id === next_id) {
				// 
				if (data[i].shop_info.id === data[i + 1].shop_info.id) {
					foodDataArray.push(data[i].food_info)
				} else {
					temp.data.food_data = foodDataArray
					foodDataArray = []
					foodInfoArray.push(temp)
				}
				data.splice(i, 1)
				i = i - 1; //i
			} else {
				// id, foodInfoArrayfood_info
				foodDataArray.push(data[i].food_info)
				temp.data.food_data = foodDataArray
				foodInfoArray.push(temp)
				data[i].food_info = foodInfoArray
				foodInfoArray = [];
				foodDataArray = []
			}
		}
	}
}
